
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'FileLogin',
components: {
XHeader,XButton
},
data(){
return{
btnLoading: false,
info: '关联档案',
GRDAID: '',
JMXM: '',
JMXBDM: '',
SFZBH: '',
JMCSRQ: '',
}

},
mounted(){

},
methods: {

selectInforMationCount(){

this.btnLoading = true;
this.$ajax.post('',{
'MSH.1':'healthFileAssociateService',
'MSH.2':'suitableFiles',
'token':this.$root.loginInfo.token,
}).then(data => {
this.btnLoading = false;
console.log('数据：'+JSON.stringify(data))
if(typeof data !='undefined'){
if(data.num==0){
this.$vux.toast.text("未检索到健康档案", "center");
}else if(data.num==1){
this.$router.push({name: "setPWD", params: {type: '1'}});
}else{

let that = this;
this.$vux.confirm.show({
title:'未检索到健康档案是否联系平台人员进行调整',

onConfirm () {
that.$router.push('/helpAndFeedback');
}
})
}
}
}).catch(error => {
console.log(error);
});
},
}
}



